const config = {
  i18n: [
    {
      langDisplay: '中文',
      locale: 'zh',
      aliases: ['zh', 'zh-cn', 'zh-hk', 'zh-sg', 'zh-tw'],
    },
    {
      langDisplay: 'English',
      locale: 'en',
      aliases: ['en', 'en-us'],
    },
  ],
};

module.exports = config;



